import React from "react";
import { Heading, VStack, useToast, Input, HStack, Text, Button, Link, Center, Image } from "native-base"

export default function PaymentSuccess(props) {
    const [voucer, setVoucher] = React.useState("12SDF409897203");
    const toast = useToast();

    async function copyTextToClipboard(text) {
        if ('clipboard' in navigator) {
            return await navigator.clipboard.writeText(text);
        } else {
            return document.execCommand('copy', true, text);
        }
    }

    // onClick handler function for the copy button
    const handleCopyClick = () => {
        // Asynchronously call copyTextToClipboard
        copyTextToClipboard(voucer)
            .then(() => {
                // If successful, update the isCopied state value
                toast.show({
                    title: "Copied to clipboard successfully!",
                    placement: "top",
                })
                
            })
            .catch((err) => {
                console.log(err);
            });
    }

    return (
        <>
            <Center space={5} mt="70">
                <Heading size="md" fontWeight="600" color="coolGray.800">
                    Your payment has been made successfully!
                </Heading>
                <Heading size="md" fontWeight="600" color="coolGray.800">
                    This is your wifi code:
                </Heading>
                <Heading size="xl" fontWeight="600" color="coolGray.800">
                    {voucer}
                </Heading>

                <Button mt="2" colorScheme="indigo" _text={{ color: 'white' }} onPress={handleCopyClick}>
                    Copy to clipboard
                </Button>

            </Center>
        </>
    );
}
